@import url("https://fonts.googleapis.com/css2?family=Kurale&display=swap");
* {
  list-style: none;
  font-family: "Kurale", serif;
}

.wrapper {
  .header {
    width: 100%;
    height: 60px;
    border-bottom: 0.0625rem solid #e5e5e5;

    .header__logo {
      text-align: center;
      img {
        height: 2.5rem;
        width: auto;
        margin: 7px;
      }
    }
  }
}

.main {
  text-align: center;
  position: relative;
  top: -75px;

  .name {
    span {
      font-size: 1.375rem;
      line-height: 1.875rem;
      font-weight: bold;
    }
  }
  .description p {
    font-size: 0.8125rem;
    line-height: 1.3125rem;
  }
}
.active {
  color: #757575;
}

.content {
  display: flex;
  flex-wrap: wrap;
  margin-top: -54px;

  a {
    text-decoration: none;
    color: #000000;
  }

  img {
    margin-top: 30px;
  }
  .product {
    text-align: left;
    margin-left: 25px;

    .desc {
      margin: 5px;
      text-align: left;

      span {
        font-weight: bold;
      }

      p {
        margin: 0px;
      }
    }
  }
}

.filters {
  height: 127px;
  position: relative;
  top: 120px;

  .content__slider {
    display: flex;
    border-bottom: 0.0625rem solid #e5e5e5;
    height: 80px;

    .color {
      button {
        background-color: #fff;
        border: 0;
        cursor: pointer;
        font-size: 1rem;
        line-height: 1.3125rem;
        margin: 40px;
      }

      img {
        transition: 0.3s ease-in-out;
        margin-left: 10px;
      }
      &:hover {
        color: #000000;

        img {
          transform: rotate(0.5turn);
        }
      }
    }
  }

  .filters__content__right {
    display: flex;
    margin: auto;
    margin-right: 45px;
    margin-top: 42px;
    p {
      color: #757575;
      font-size: 1rem;
      line-height: 1.3125rem;
      margin: 0px;
    }

    .strip {
      height: 0.75rem;
      border-right: 0.0625rem solid #757575;
      margin-left: 15px;
      margin-right: 15px;
      margin-top: 5px;
    }

    .span {
      font-size: 1rem;
      line-height: 1.3125rem;
    }

    img {
      margin-left: 10px;
      transition: 0.3s ease-in-out;
    }
    .sorting {
      cursor: pointer;
      margin-top: -2px;

      span {
        position: relative;
      }

      &:hover {
        img {
          transform: rotate(0.5turn);
        }
      }
    }
  }
  .sorting_down {
    height: 200px;
    width: 1440px;
    background-color: #fff;
    position: absolute;

    ul {
      text-align: left;
      font-size: 1rem;
      line-height: 2rem;
      padding: 20px;
      margin-left: 30px;
      position: absolute;
    }

    .active1 {
      font-weight: 700;
      font-size: 1.1rem;
    }

    li:hover {
      color: #757575;
      cursor: pointer;
    }
  }
}

.navigation__tabs {
  background-color: #fff;
  width: 100%;
  height: 62px;
  margin-top: -15px;
  font-size: 15px;
  border-bottom: 0.0625rem solid #e5e5e5;
  position: sticky;
  top: 0;
  z-index: 1;

  ul {
    display: flex;
    position: relative;
    // top: 25%;
    top: 35%;
  }

  li {
    cursor: pointer;
    margin-right: 20px;
    transition: 0.5s ease-in-out;

    &:hover {
      color: #757575;
    }
  }
  img {
    position: relative;
    bottom: 20px;
    left: 1350px;
    cursor: pointer;
  }
}

.cart {
  img {
    margin-left: 670px;
    margin-top: 200px;
    animation: circle 10s ease-in-out;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}

@keyframes circle {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.not__found__block {
  background-color: #ececec;
  height: 820px;
  width: 101%;
  position: relative;
  bottom: 5px;
  right: 7px;

  img {
    width: 700px;
    height: 300px;
    margin-top: 200px;
    margin-left: 350px;
  }

  h1 {
    margin-left: 335px;
  }
  button {
    margin-left: 615px;
    width: 168px;
    height: 48px;
    border-radius: 20px;
    background: #ac3634;
    border: none;
    cursor: pointer;
    color: #fff;
    font-size: 15px;
  }
}
